<template>
    <div class="parend-box">
         <div class="parend-top">
            <img src="../../img/use+.png" alt="">
            <p>
                <span v-for="i,index in recut" @click="cut(index)" :class="{'addwhite':i.show?'addwhite':''}">{{i.title}}</span>
            </p>
            <canvas width="24" height="22"></canvas>
          </div>
          <div class="Hair-what">
              <p>
                  <img src="../../img/dta.png" alt="">
                  <span></span>
              </p>
              <p>
                  <img src="../../img/sort.png" alt="">
                  <span></span>
              </p>
          </div>
          <p class="hr"></p>
          <div class="hot-topic">
              <p class="hotitle"><img src="../../img/jtou-right.png" alt=""></p>
              <div class="hot-div">
                  <p>##</p>
                  <p>##</p>
              </div>
          </div>
          <div class="send-videobox">
              <div class="send-top">
                  <img src="../../img/u=746478563,2459179995&fm=27&gp=0.jpg" alt="">
                  <div>
                      <p>1</p>
                      <p>11:00</p>
                  </div>
                  <span class="sort-t">:</span>
                  <span class="fllow">+</span>
              </div>
              <p class="test-vido">1</p>
              <div class="G-video"><video src="../../video/movie.mp4" controls></video></div>
              <p class="watch-v">live</p>
              <p class="p-flex">
                  <span><img src="../../img/zan.png" alt="">43</span>
                  <span><img src="../../img/plun.png" alt="">33</span>
                  <span><img src="../../img/tarmint.png" alt="">22</span>
                  <span class="dot"><img src="../../img/dot.png" alt=""></span>
              </p>
          </div>
    </div>
</template>
<script>
export default {
     mounted(){
        // 
        this.$attrs.appfu.gets=true;
        
    },
    data(){
        return{
            recut:[
                {title:'',show:true},
                {title:'',show:false}
            ]
        }
    },
    methods:{
        cut(index){
            for(let i=0;i<this.recut.length;i++){
                this.recut[i].show=false;
            };
            this.recut[index].show=true;
        }
    }
}
</script>



