<template>
    <div class="login-box">
         <div class="login-top">
             <img src="../../img/white-j.png" alt="" @click="reback">
             <p></p>
          </div>
          <div class="login-input">
              <div>
                  <img src="../../img/ipone.png" alt="">
                  <input type="number" placeholder="" ref="mobiles" @blur="moblur">
              </div>
              <p ref="mos">{{mos}}</p>
              <div>
                   <img src="../../img/password.png" alt="">
                  <input type="password" placeholder="" ref="pass" @blur="passblur">
              </div>
               <p ref="pos">{{pas}}</p>
          </div>
          <button class="login-bt" @click="logins"></button>
          <p class="rest"></p>
    </div>
</template>
<script>
export default {
    created(){
     
    },
    data(){
        return{
            mos:'',
            pas:''
        }
    },
    methods:{
        reback(){
            this.$router.go(-1);
        },
        moblur(){
            console.log(this,'dd');
            // 
            if(this.$refs.mobiles.value==''){
                this.$refs.mos.style.display='block';
                this.mos=''
            }else if(!/^((1[3,5,8][0-9])|(14[5,7])|(17[0,6,7,8])|(19[7]))\d{8}$/.test(this.$refs.mobiles.value)){
                 this.$refs.mos.style.display='block';
                 this.mos=''
            }else{
                 this.$refs.mos.style.display='none';
            }
        },
        passblur(){
            // 
             if(this.$refs.pass.value==''){
                  this.$refs.pos.style.display='block';
                 this.pas=''
            }else
            if(!/^\d{6}$/.test(this.$refs.pass.value)){
                   this.$refs.pos.style.display='block';
                     this.pas=''
            }else{
                 this.$refs.pos.style.display='none';
            }
        },
        logins(){
            console.log(1)
            if(this.$refs.mobiles.value&&this.$refs.pass.value){
                alert('');
            }
        }
    }
}
</script>



